document.addEventListener('DOMContentLoaded', function() {

	var swiperBestseller = new Swiper('.bestseller-games-list', {
		loop: false,
		autoplay: false,
		spaceBetween: 16,
		slidesPerView: 6,
		speed: 500,
		grabCursor: true,
		navigation: {
			nextEl: '.similar-right',
			prevEl: '.similar-left',
		},
	});
});

